import React, {useState} from 'react'
import moment from "moment";
import { Row, Col, Card, Form, DatePicker, Modal, InputNumber, Select, Button, Input } from 'antd';
import { Typography } from "antd";

const { Title } = Typography;
const { Option } = Select;

const rules = {
	departure: [
		{
			required: true,
			message: 'Select departure location',
		}
	],
	destination: [
		{
			required: true,
			message: 'Select destination location'
		}
	],
	qty: [
		{
			required: true,
			message: 'Enter quantity to ship (in tons)'
		}
	],
	item: [
		{
			required: true,
			message: 'Please enter item name',
		}
	],
	client: [
		{
			required: true,
			message: 'Client name is required',
		}
	],
	date: [
		{
			required: true,
			message: 'Starting date is required',
		}
	],
	price: [
		{
			required: true,
			message: 'Price is required',
		}
	],
	currency: [
		{
			required: true,
			message: 'currency is required',
		}
	],
	rate: [
		{
			required: true,
			message: 'rate is required',
		}
	],
}

const GeneralField = props => {
	const {destinations, destinationsLoading, currencies, currenciesLoading, totalAmount, handlePriceChange, handleRateChange} = props;
	

	const disabledDate = (current) => {
		let response = false;
		if(current && (current < moment().startOf("day"))) response = true;
		return current && response;
	}

	return (
		
	<Row gutter={16} className='justify-content-center'>
		
		<Col xs={24} sm={24} md={17}>
			<Card>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="client" label="Client name" rules={rules.client}>
							<Input className="w-100"/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="date" label="Starting date" rules={rules.date}>
							<DatePicker className="w-100" format="DD-MM-YYYY" disabledDate={disabledDate}/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="item" label="Item to ship" rules={rules.item}>
							<Input className="w-100"/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="quantity" label="Quantity to ship (Tonnage)" rules={rules.qty}>
							<InputNumber
								className="w-100"
								min={0}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="departure" label="Departure Address" rules={rules.departure}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select departure"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((address, index) => (
									<Option key={index} value={address.data()._id}>
										{address.data().name+' - '+address.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="destination" label="Destination Address" rules={rules.destination}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select destination"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((address, index) => (
									<Option key={index} value={address.data()._id}>
										{address.data().name+' - '+address.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="price" label="Price" rules={rules.price}>
							<InputNumber
								className="w-100"
								min={0}
								onChange={handlePriceChange}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="currency" label="Currency" rules={rules.currency}>
							<Select
								className="w-100 d-block"
								placeholder="- Select Currency -"
							>
								{!currenciesLoading ? currencies.docs.map((currency)=><Option value={currency.data()._id} className='w-100 pe-5'>{currency.data().name}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Option>) : null }
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="rate" label="Exchange Rate vs 1RWF" rules={rules.rate}>
							<InputNumber
								className="w-100"
								min={0}
								onChange={handleRateChange}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item label="Total Paid in RWF">
							<InputNumber
								className="w-100"
								min={0}
								value={totalAmount}        
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
				</Row>
			</Card>
		</Col>
	</Row>
)}

export default GeneralField
